'use client';

import { useContext } from 'react';
import { AppContext, AppContextType } from '@/contexts/app-context';
import { InventoryManagement } from '@/components/inventory/inventory-management';
import { ExpenseManagement } from '@/components/expenses/expense-management';

export default function InventoryPage() {
  const { 
    inventoryItems, 
    addInventoryItem, 
    updateInventoryItem, 
    removeInventoryItem,
    expenses,
    addExpense,
    removeExpense,
    t
  } = useContext(AppContext) as AppContextType;

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">{t('inventory-and-expenses')}</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div>
          <h2 className="text-xl font-semibold mb-4">{t('inventory-management')}</h2>
          <InventoryManagement 
            inventoryItems={inventoryItems}
            onAddItem={addInventoryItem}
            onUpdateItem={updateInventoryItem}
            onRemoveItem={removeInventoryItem}
          />
        </div>
        <div>
          <h2 className="text-xl font-semibold mb-4">{t('expense-management')}</h2>
          <ExpenseManagement 
            expenses={expenses}
            onAddExpense={addExpense}
            onRemoveExpense={removeExpense}
          />
        </div>
      </div>
    </div>
  );
}
